body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-poppins: Poppins;
  --font-inherit: inherit;

  /* font sizes */
  --font-size-78xl-3: 97.3px;
  --font-size-14xl-5: 33.5px;
  --font-size-base-5: 16.5px;
  --font-size-mini-2: 14.2px;
  --font-size-base-3: 15.3px;
  --font-size-lgi-5: 19.5px;
  --font-size-xl-7: 20.7px;
  --font-size-16xl-7: 35.7px;
  --font-size-lgi-4: 19.4px;
  --font-size-smi: 13px;
  --font-size-smi-9: 12.9px;
  --font-size-mid-3: 17.3px;
  --font-size-8xl-4: 27.4px;
  --font-size-xl: 20px;
  --font-size-2xl-9: 21.9px;
  --font-size-2xs-8: 10.8px;
  --font-size-25xl-2: 44.2px;
  --font-size-33xl-2: 52.2px;
  --font-size-mid-9: 17.9px;
  --font-size-lgi-9: 19.9px;

  /* Colors */
  --color-white: #fff;
  --color-gray-100: #878585;
  --color-gray-200: rgba(255, 255, 255, 0);
  --color-black: #000;
  --color-whitesmoke: #f0f0f0;
  --color-gainsboro: rgba(217, 217, 217, 0);
  --color-orangered-100: #e63a05;
  --color-darkgray: #989898;
  --color-dimgray: #696969;
  --color-lightgray: #cacaca;

  /* Paddings */
  --padding-3xs-2: 9.2px;
  --padding-xl: 20px;
  --padding-mid: 17px;
  --padding-7xs: 6px;

  /* border radiuses */
  --br-lg-5: 18.5px;
  --br-26xl-8: 45.8px;
  --br-base-8: 15.8px;
  --br-6xl-9: 25.9px;
  --br-smi-1: 12.1px;
}
